#desktop{
    display:none;
}
#tablet{
    display:none;
}

#artwork:hover {
    opacity: 0.8;
}

@media only screen and (min-width:768px){
    #mobile{
        display:none;
    } 
    #tablet{
        display:inline;
    }
    #desktop{
        display:none;
    }
}


@media only screen and (min-width:1300px){
    #mobile{
        display:none;
    } 
    #tablet{
        display:none;
    }
    #desktop{
        display:inline;
    }
}